body {
    font-size: 10px;
}

p {
    margin-top: 0px;
    margin-block-end: 0px;
}

.card {
    width: 41em;
    height: 58em;
    padding: 1.5em;
    border: 1.5em solid #ffea00;
    border-radius: 1.5em;
    background-size: cover;
    margin: 3em;
    transition: all .3s ease-in-out;
}

.card:hover {
    transform: scale(1.1); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
    transform: translate(0,-2em);
    box-shadow: 0.6em 0.6em 3em 0.3em #777777;
}

.center {
    padding-left: 1em;
    padding-right: 1em;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.topBar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.health {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: flex-end;
}

.health p {
    font-size: 2em;
    font-weight: bold;
}

.energy {
    padding: 0.3em;
    height: 2em;
    width: 2em;
}

.normal {  
    height: 2.75em;
    width: 2.75em;
}

.mini {  
    height: 1.5em;
    width: 1.5em;
}

.pokemon-identity {
    display: flex;
}

.pokemon-stage {
    font-size: 0.7em;
}

.pokemon-name {
    font-size: 2em;
    font-weight: bold;
}

.pokemon-devolution-name {
    font-size: 0.85em;
}

.pokemon-devolution {
    padding-top: 0.5em;
}

.pokemon-devolution-miniature {
    height: 2em;
    width: 2em;
}

.cardImage img {
    width: 37.5em;
    height: 29em;
}

.background {
    position: relative;
    top: 0;
    left: 0;
    padding: 0.5em;
    box-shadow: 0.2em 0.2em 1em 0.1em black;
    background: radial-gradient(circle, rgba(153,132,24,1) 0%, rgba(218,189,30,1) 41%, rgba(153,132,24,1) 65%, rgba(215,189,40,1) 73%, rgba(227,209,105,1) 100%);
}

.pokemon {
    position: absolute;
    top: 0;
    left: 0;
  }

.cardImage {
    width: 38em;
    position: relative;
    top: 0;
    left: 0;
}


/* POKEMON DESCRIPTION AND CARD DESCRIPTION */
.pokemon-and-card-description {
    margin-top: 0.25em;
    height: 2.5em;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.pokemon-description p {
    width: 33em;
    font-size: 1em;
    font-weight: 900;
    text-align: center;
    color:black !important;
    background: linear-gradient(90deg, rgba(153,132,24,1) 0%, rgba(218,189,30,1) 41%, rgba(227,209,105,1) 65%, rgba(215,189,40,1) 73%, rgba(153,132,24,1) 100%);
}

/* POKEMON RANDOM FACT BOX */
.pokemon-random-fact-box {
    margin-top: 0.3em;
    margin-left: 1em;
    margin-right: 1em;
    padding-left: 1em;
    padding-right: 1em;
    font-weight: bold;
    font-size: 0.95em;
    border: 2px solid rgba(153,132,24,1);
}

.white {
    color: #fff;
}